'use client'

import { useEffect, useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { ChevronLeft, ChevronRight, Play } from 'lucide-react'
import { Anime } from '@/lib/api'

interface HeroCarouselProps {
  animes: Anime[]
}

export default function HeroCarousel({ animes }: HeroCarouselProps) {
  const [index, setIndex] = useState(0)

  useEffect(() => {
    if (animes.length === 0) return
    const interval = setInterval(() => {
      setIndex((prev) => (prev + 1) % animes.length)
    }, 4500)
    return () => clearInterval(interval)
  }, [animes])

  if (!animes.length) return null
  const anime = animes[index]

  return (
    <div className="relative w-full h-[70vh] overflow-hidden rounded-xl bg-black/20 shadow-lg">
      <Image
        src={anime.images.jpg.large_image_url}
        alt={anime.title}
        fill
        priority
        className="object-cover opacity-80"
      />

      <div className="absolute inset-0 bg-gradient-to-t from-black via-black/20 to-transparent"></div>

      <div className="absolute bottom-10 left-10 text-white max-w-xl">
        <h1 className="text-4xl font-bold mb-4 drop-shadow-lg">{anime.title}</h1>
        <p className="text-white/80 mb-6 line-clamp-3">{anime.synopsis || 'No synopsis available'}</p>
        <Link href={`/anime/${anime.mal_id}`}>
          <button className="flex items-center gap-2 px-6 py-3 bg-white/90 text-black rounded-full font-semibold hover:bg-white transition">
            <Play size={20} /> Watch Now
          </button>
        </Link>
      </div>

      {/* Navigation Buttons */}
      <button
        onClick={() => setIndex((index - 1 + animes.length) % animes.length)}
        className="absolute left-4 top-1/2 -translate-y-1/2 bg-black/40 hover:bg-black/60 p-3 rounded-full transition"
      >
        <ChevronLeft size={32} className="text-white" />
      </button>

      <button
        onClick={() => setIndex((index + 1) % animes.length)}
        className="absolute right-4 top-1/2 -translate-y-1/2 bg-black/40 hover:bg-black/60 p-3 rounded-full transition"
      >
        <ChevronRight size={32} className="text-white" />
      </button>
    </div>
  )
}
